.Contact{
   min-width: 150px;
    padding: 10px 10px;
    background-color: #eee;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    margin:5px;
    -webkit-box-shadow: 1px 1px 1px #fff;
            box-shadow: 1px 1px 1px #fff;
    -webkit-transition: -webkit-transform 0.5s ease;
    transition: -webkit-transform 0.5s ease;
    -o-transition: transform 0.5s ease;
    transition: transform 0.5s ease;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}
.col-wise{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

}
.col-wise h2{
    padding: 0;
    margin: 2px;
}
.col-wise h3{
    font-size: 20px;
    font-weight: 400;
    color: #555;
    padding: 0;
    margin: 5px;
}
.Contact .Contactinfo{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
   min-width: 150px;
    
}
.row-wise{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.row-wise button{
    background-color: inherit;
border: 2px solid rgb(255, 10, 10);
    border-radius: 50px;
    color: rgb(255, 10, 10);
    font-size: 700;
    padding: 2px 10px;
    font-size: 15px;
