    color: white;
}
.row-wise button:first-of-type{
    border: 2px solid rgb(255, 210, 10);
    border-radius: 50px;
    color: rgb(255, 210, 10);
    font-size: 700;
    padding: 2px 10px;
    font-size: 15px;
}
.row-wise button:first-of-type:hover{
    background-color: rgb(255, 210, 10);
    color: white;
}
.Contact:hover{
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}
.Contact .imageContact{
    background-color: #ccc;
    border-radius: 150px;
    width: 70px;
    height: 70px
}
.editName{
    border: 1px solid gray;
    border-radius: 15px;
    margin-bottom: 2px;
    padding: 5px;
}
.editMobile{
    border: 1px solid gray;
    border-radius: 15px;
    margin-bottom: 2px;
    padding: 5px;
}
.loading{
    height: 116px;
}
